<template>
    <el-form :model="form" :rules="rules" ref="form" label-width="150px">
      <el-steps :active="active" finish-status="success">
        <el-step title="初始化表单"></el-step>
        <el-step title="股本与股东"></el-step>
        <el-step title="首任秘書"></el-step>
        <el-step title="首任董事" :status="forthStatus"></el-step>
      </el-steps>

      <!-- page1 -->
      <el-row v-show="active == 0" class="ncc1-page">
        <el-row class="nnc1-module-title">A.填写公司基础信息 Basic Information Company</el-row>
        <el-row class="form-item-container">
          <el-form-item label="法定文件版本">
            <el-radio-group v-model="form.language">
              <el-radio label="繁体中文　Traditional" value="cn"></el-radio>
              <el-radio label="英文　English" value="en"></el-radio>
            </el-radio-group>
          </el-form-item>
          <el-form-item label="公司英文名稱:" prop="companyNameEn">
            <el-input v-model="form.companyNameEn" placeholder="公司英文名称只能由字母以及符号和空格组成并以 LIMITED 结尾"></el-input>
          </el-form-item>
          <el-form-item label="公司中文名稱:" prop="companyNameCn">
            <el-input v-model="form.companyNameCn" placeholder="公司中文名称只能由数字或中文汉字以及符号与空格并以有限公司结尾"></el-input>
          </el-form-item>
          <el-form-item label="註冊辦事處地址:" prop="companyRegistAddr">
            <el-input v-model="form.companyRegistAddr" placeholder="公司所使用的註冊辦事處的建議地址"></el-input>
          </el-form-item>
        </el-row>
        <el-row class="nnc1-module-title">B.提交人的资料　Presentor’s Reference</el-row>
        <el-row class="form-item-container">
          <el-form-item label="提交人信息">
            <el-radio-group v-model="submitUser.userInfo">
              <el-radio label="默认" value="defaultUser"></el-radio>
              <el-radio label="自定义" value="selfUser"></el-radio>
            </el-radio-group>
          </el-form-item>
          <el-form-item label="公司名稱:" prop="submitUserName">
            <el-input v-model="form.submitUserName" placeholder="提交人的中文名称"></el-input>
          </el-form-item>
          <el-form-item label="提交人地址:" prop="submitUserAddr">
            <el-input v-model="form.submitUserAddr" placeholder="提交人的地址"></el-input>
          </el-form-item>
          <el-row>
            <el-col :span="12">
              <el-form-item label="提交人電話:" prop="submitUserPhone">
                <el-input v-model="form.submitUserPhone" placeholder="提交人電話"></el-input>
              </el-form-item>
            </el-col>
            <el-col :span="12">
              <el-form-item label="提交人傳真:" prop="submitUserFax">
                <el-input v-model="form.submitUserFax" placeholder="提交人傳真"></el-input>
              </el-form-item>
            </el-col>
          </el-row>
          <el-form-item label="提交人電郵:" prop="submitUserEmail">
              <el-input v-model="form.submitUserEmail" placeholder="提交人電郵"></el-input>
          </el-form-item>
        </el-row>
      </el-row>

      <!-- page2 -->
      <el-row v-show="active == 1" class="ncc1-page">
        <el-row class="nnc1-module-title">A：股本信息　Share Capital</el-row>
        <el-row class="form-item-container">
            <el-row>
              <el-col :span="12">
                <el-form-item label="股份的類別:">
                    <el-input v-model="form.companyStockType" placeholder="股份的類別"></el-input>
                </el-form-item>
              </el-col>
              <el-col :span="12">
                <el-form-item label="貨幣:">
                    <el-input v-model="form.companyStockCurrency" placeholder="貨幣(HKD)"></el-input>
                </el-form-item>
              </el-col>
            </el-row>
            <el-row>
                <el-form-item label="建議發行的股份總數:">
                    <el-input v-model="form.companyStockTotal" placeholder="建議發行的股份總數(数字)"></el-input>
                </el-form-item>
            </el-row>
        </el-row>
        <el-row class="nnc1-module-title">B：创办成员/股东信息　Founder Members</el-row>
        <el-row class="form-item-container">
            <el-row>
              <el-col :span="12">
                <el-form-item label="中文姓名:">
                    <el-input v-model="form.funderNameCn" placeholder="必须为中文"></el-input>
                </el-form-item>
              </el-col>
              <el-col :span="12">
                <el-form-item label="英文姓名:">
                    <el-input v-model="form.funderNameEn" placeholder="姓和名必须以'-'分割，如WU-JIANMIN"></el-input>
                </el-form-item>
              </el-col>
            </el-row>
            <el-form-item label="地址:" prop="funderAddr">
              <el-input v-model="form.funderAddr" placeholder="联络地址或证件地址"></el-input>
            </el-form-item>
            <el-row>
              <el-col :span="12">
                <el-form-item label="國家／地區:">
                    <el-input v-model="form.funderNameCn" placeholder="必须为中文"></el-input>
                </el-form-item>
              </el-col>
              <el-col :span="12">
                <el-form-item label="占股份的数目:">
                    <el-input v-model="form.funderNameEn" placeholder="小于發行股份總數"></el-input>
                </el-form-item>
              </el-col>
            </el-row>
            <el-form-item>
              <el-checkbox-group v-model="form.type">
                <el-checkbox label="作为董事 (如作为董事该信息将体现在董事栏)" name="type"></el-checkbox>
              </el-checkbox-group>
            </el-form-item>
        </el-row>
      </el-row>

      <!-- page3 -->
      <el-row v-show="active == 2" class="ncc1-page">
        <el-row class="nnc1-module-title">首任秘书　First Secretary</el-row>
        <el-row class="form-item-container">
            <el-row>
              <el-col :span="12">
                <el-form-item label="公司編號:">
                    <el-input v-model="form.companyNumber" placeholder="公司編號"></el-input>
                </el-form-item>
              </el-col>
              <el-col :span="12">
                <el-form-item label="公司邮件:">
                    <el-input v-model="form.companyEmail" placeholder="公司邮件(NIL)"></el-input>
                </el-form-item>
              </el-col>
            </el-row>
            <el-form-item label="公司中文名稱:">
                <el-input v-model="form.secretaryNameCn" placeholder="公司中文名稱"></el-input>
            </el-form-item>
            <el-form-item label="公司英文名稱:">
                <el-input v-model="form.secretaryNameEn" placeholder="公司英文名稱"></el-input>
            </el-form-item>
            <el-form-item label="香港地址:">
                <el-input v-model="form.secretaryHKAddr" placeholder="英文地址"></el-input>
            </el-form-item>
        </el-row>
      </el-row>

      <!-- page4 -->
      <el-row v-show="active == 3" class="ncc1-page">
        <el-row class="nnc1-module-title">首任董事　First Directors</el-row>
        <el-row class="form-item-container">
          <el-row>
            <el-col :span="8">
              <el-form-item label="董事姓名:">
                  <el-input v-model="form.chairmanNameCn" placeholder="中文姓名"></el-input>
              </el-form-item>
            </el-col>
            <el-col :span="8">
              <el-form-item label="英文姓名:">
                  <el-input v-model="form.chairmanSureName" placeholder="英文姓式"></el-input>
              </el-form-item>
            </el-col>
            <el-col :span="8">
              <el-form-item label="英文姓名:">
                  <el-input v-model="form.chairmanOtherName" placeholder="英文名字"></el-input>
              </el-form-item>
            </el-col>
          </el-row>
          <el-form-item label="國家／地區:">
              <el-input v-model="form.chairmanCountry" placeholder="國家／地區"></el-input>
          </el-form-item>
          <el-form-item label="住址:">
              <el-input v-model="form.chairmanAddr" placeholder="联络地址或证件地址"></el-input>
          </el-form-item>
          <el-form-item label="证件类型:">
              <el-input v-model="form.chairmanAddr" placeholder="证件类型"></el-input>
          </el-form-item>
          <el-row>
            <el-col :span="12">
              <el-form-item label="姓名簽署:">
                  <el-input v-model="form.funderNameSigned" placeholder="姓名簽署"></el-input>
              </el-form-item>
            </el-col>
            <el-col :span="12">
              <el-form-item label="簽署日期:">
                <el-date-picker type="date" placeholder="选择日期" v-model="form.funderDateSigned" style="width: 100%;"></el-date-picker>
              </el-form-item>
            </el-col>
          </el-row>
        </el-row>
      </el-row>

      <el-row class="btn-row">
        <el-button @click="prePage" v-show="active !== 0">上一步</el-button>
        <el-button @click="nextPage" v-show="active !== 3">下一步</el-button>
        <el-button @click="nextPage" v-show="active == 3">提交</el-button>
      </el-row>
    </el-form>
</template>

<script>
import submitUserInfo from '@/config/submitUser'

export default {
  data() {
    return {
      active: 0,
      forthStatus: 'wait',
      form: {
        language: '',
        companyNameEn: '',
        companyNameCn: '',
        companyRegistAddr: '',
        submitUserName: submitUserInfo.userName,
        submitUserAddr: submitUserInfo.userAddr,
        submitUserPhone: submitUserInfo.userPhone,
        submitUserFax: submitUserInfo.userFax,
        submitUserEmail: submitUserInfo.userEmail,
        companyStockType: submitUserInfo.companyStockType,
        companyStockCurrency: '',
        companyStockTotal: '',
        funderNameCn: '',
        funderNameEn: '',
        funderAddr: '',

        type: '',

        companyNumber: '',
        companyEmail: '',
        secretaryNameCn: '',
        secretaryNameEn: '',
        secretaryHKAddr: '',

        chairmanNameCn: '',
        chairmanSureName: '',
        chairmanOtherName: '',
        chairmanCountry: '',
        chairmanAddr: '',

        funderNameSigned: '',
        funderDateSigned: ''
      },
      submitUser: {
        userInfo: 'defaultUser'
      },
      rules: {

      }
    }
  },

  methods: {
    prePage() {
      if (this.active > 0) {
        this.active = this.active - 1
      }    
      this.forthStatus = 'wait'
    },

    nextPage() {
      if (this.active < 3) {
        this.active = this.active + 1
      } else {
        this.forthStatus = 'success'
        console.log(this.active)
      } 
    }
  }
}
</script>

<style scoped>
.ncc1-page {
  margin-top: 20px;
}

.form-item-container {
  width: 80%;
  margin: 0 auto;
}

.nnc1-module-title {
  padding: 10px 0;
  border-bottom: 1px solid #333;
  margin-bottom: 16px;
}

.btn-row {
  width: 80%;
  margin: 0 auto;
  text-align: right;
}
</style>





